// 파일을 분리할 때 import / export 사용
// export default : 다른 파일에서도 해당 파일을 사용가능
// 단일변수 내보내기 : export default 변수명
// 다중변수 내보내기 : export {변수1, 변수2}
// 가져오기 : import 변수명 from 경로
export default [
    {
      id : 0,
      title : "White and Black",
      content : "Born in France",
      price : 120000
    },
  
    {
      id : 1,
      title : "Red Knit",
      content : "Born in Seoul",
      price : 110000
    },
  
    {
      id : 2,
      title : "Grey Yordan",
      content : "Born in the States",
      price : 130000
    }
]